// src/SenderPage.js
import React, { useState, useEffect, useRef } from "react";
import Grid from "./Grid";
import { db, set, ref } from "./firebase";

const SenderPage = () => {
  const [text, setText] = useState("");
  const [timeLevel, setTimeLevel] = useState(7);
  const [outsideLevel, setOutsideLevel] = useState(2);
  const [feelingLevel, setFeelingLevel] = useState(4);
  const [insideLevel, setInsideLevel] = useState(1);

  const [overlayVisible, setOverlayVisible] = useState(true);
  const [lastInteraction, setLastInteraction] = useState(Date.now());
  const idleTimeout = 5 * 60 * 1000; // 5 perc
  const senderRef = useRef(null);

  useEffect(() => {
    if (senderRef.current) {
      senderRef.current.focus();
    }
  }, []);

  useEffect(() => {
    const handleInteraction = () => {
      setLastInteraction(Date.now());
      setOverlayVisible(false);
    };

    window.addEventListener("mousemove", handleInteraction);
    window.addEventListener("keydown", handleInteraction);

    return () => {
      window.removeEventListener("mousemove", handleInteraction);
      window.removeEventListener("keydown", handleInteraction);
    };
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      if (Date.now() - lastInteraction > idleTimeout) {
        setOverlayVisible(true);
      }
    }, 1000);
    return () => clearInterval(interval);
  }, [lastInteraction]);

  useEffect(() => {
    const data = {
      text,
      timeLevel,
      outsideLevel,
      feelingLevel,
      insideLevel,
    };
    set(ref(db, "sharedData"), data);
  }, [text, timeLevel, outsideLevel, feelingLevel, insideLevel]);

  const handleKeyDown = (e) => {
    if (e.key.length === 1) setText((prev) => prev + e.key);
    else if (e.key === "Backspace") setText((prev) => prev.slice(0, -1));
  };

  return (
    <div
      ref={senderRef}
      tabIndex={0}
      onKeyDown={handleKeyDown}
      style={{ backgroundColor: "black", height: "100vh" }}
    >
      {overlayVisible && (
        <div
          style={{
            position: "absolute",
            top: 0,
            left: 0,
            width: "100vw",
            height: "100vh",
            backgroundColor: "black",
            color: "white",
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            fontSize: "24px",
            flexDirection: "column",
            textAlign: "center",
            fontFamily: "monospace",
            zIndex: 9999,
            pointerEvents: "none",
            gap: "16px",
          }}
        >
          <div>Information is available, yet inaccessible.</div>
          <div>Reception and processing are not givens, but struggles —</div>
          <div>in a world not built for your nervous system.</div>
          <div>Say what’s hard to put into words:</div>
        </div>
      )}

      <Grid
        text={text}
        timeLevel={timeLevel}
        setTimeLevel={setTimeLevel}
        outsideLevel={outsideLevel}
        setOutsideLevel={setOutsideLevel}
        feelingLevel={feelingLevel}
        setFeelingLevel={setFeelingLevel}
        insideLevel={insideLevel}
        setInsideLevel={setInsideLevel}
      />
    </div>
  );
};

export default SenderPage;
