// src/ReceiverPage.js
import React, { useState, useEffect } from "react";
import AnimatedGrid from "./AnimatedGrid";
import { db, ref, onValue } from "./firebase";

const ReceiverPage = () => {
  const [text, setText] = useState("");
  const [timeLevel, setTimeLevel] = useState(7);
  const [outsideLevel, setOutsideLevel] = useState(2);
  const [feelingLevel, setFeelingLevel] = useState(4);
  const [insideLevel, setInsideLevel] = useState(1);

  useEffect(() => {
    const dataRef = ref(db, "sharedData");
    const unsubscribe = onValue(dataRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        setText(data.text || "");
        setTimeLevel(data.timeLevel || 7);
        setOutsideLevel(data.outsideLevel || 2);
        setFeelingLevel(data.feelingLevel || 4);
        setInsideLevel(data.insideLevel || 1);
      }
    });
    return () => unsubscribe();
  }, []);

  return (
    <AnimatedGrid
      text={text}
      timeLevel={timeLevel}
      outsideLevel={outsideLevel}
      feelingLevel={feelingLevel}
      insideLevel={insideLevel}
    />
  );
};

export default ReceiverPage;
